* {  
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;

}
.App {
  text-align: center;
}
html, body, #root {
  width: 100%;
  height: 100%;
}
#root {
  display: flex;
  flex-direction: column;
  align-items: center;
}
nav {
  background: #FFF7ED;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 36px 24px 36px 24px;
}
.main {
  min-width: 300px;
  max-width: 550px;
  width: 100vw;
  height: 100%;
  display: flex;
  flex-direction: column;
}
nav a {
  text-decoration: none;
  color: #4D4D4D;
  font-size: 16px;
  font-weight: 600;  
}
nav a:hover {
  text-decoration: underline;
}
.menu a {
  margin-left: 24px;
}
.logo {
  color: #000;
  font-size: 24px;
  font-weight: 900;
}
.content {
  background: #FFF7ED;
}
.home-wrapper {
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.46) 0%, rgba(0, 0, 0, 0.46) 100%), lightgray 0px -65.191px / 100% 178.109% no-repeat;
  padding: 26px;
  color: #FFF;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.home-wrapper h2 {
  font-size: 36px;
  font-weight: 800;
}
.home-wrapper p {
  font-size: 16px;
  font-weight: 500;
}
.home-wrapper .link-btn {
  border-radius: 5px;
  background: #FF8C38; 
  height: 50px;
  border: none;
  width: 100%;
  color: #FFF;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  font-style: normal;
  font-weight: 700; 
  margin-top: 40px;
  text-decoration: none;
}
.home-wrapper .link-btn:hover {
  text-decoration: underline;
  cursor: pointer;  
}
.about-wrapper {
  color: #161616;
  display: flex;
  flex-direction: column;
  justify-content: space-between;  
  padding: 26px;

}
.about-wrapper h2 {
  font-size: 32px;
  font-weight: 700;
  margin:0;
  padding-block: 12px;
}
.about-wrapper p {
  font-size: 16px;
  font-weight: 500;
  margin: 0;
  padding-block: 12px;
}
.about-destination {
  border-radius: 5px;
  background: #FFCC8D; 
  padding: 6px 37px 32px 37px;
  margin-top: 44px;
}
.about-destination h3 {
  font-size: 24px;
  font-weight: 700;  
}
.about-destination .link-btn {
  display: inline-flex;
  padding: 12px 22px 13px 22px;
  justify-content: center;
  align-items: center; 
  border-radius: 10px;
  background: #161616;
  color: #fff;
  cursor: pointer;
  border: none;
  font-size: 16px;
  font-weight: 700;
  text-decoration: none;
}
.about-destination .link-btn:hover {
  text-decoration: underline;
}

footer h4 {
  color: #AAA;
  text-align: center;
  font-size: 14px;
  font-weight: 500;
}
footer {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-shrink: 0; 
  background: #252525; 
}

/* vans */
.vans-wrapper {
  padding-inline: 23px;
  padding-bottom: 42px;
}

.vans-list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-items: center;
  gap: 34px;
  margin-top: 57px;
}
.van-info {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  
}
.van-info > h4 > span {
  font-size: 9px;
}
.van-tile a {
  color: #161616;
  text-decoration: none;    
}

.van-tile img {
  max-width: 100%;
  border-radius: 5px;
}

.vans-wrapper button {
  margin-right: 20px;
  cursor: pointer;
}

.van-type {
  height: 34px;
  padding: 6px 26px;
  font-style: normal;
  font-weight: 500;
  border: none;
  border-radius: 5px;
  background-color: #FFEAD0;
  color: #4D4D4D;
  transition: 200ms all cubic-bezier(0.4, 0, 0.2, 1);
}

.van-type:hover {
  color: #FFEAD0;
}

.van-type:focus {
  outline: none;
}

.van-type.selected {
  color: #FFEAD0;
}

.van-type.simple:hover, .van-type.simple.selected {
  background-color: #E17654;
}

.van-type.rugged:hover, .van-type.rugged.selected {
  background-color: #115E59;
}

.van-type.luxury:hover, .van-type.luxury.selected {
  background-color: #161616;
}

.van-type.clear-filters {
  margin-left: -20px;
  height: 34px;
  padding: 6px 26px;
  font-style: normal;
  font-weight: 500;
  border: none;
  border-radius: 5px;
  text-decoration: underline;
  background-color: transparent;
  color: #4D4D4D;
}

i[class*='van-type-'] {
  padding: 7px 14px;
}